import React from "react";
import { Link, Route, Switch } from "react-router-dom";
import Home from "./pages/Home";
import Favorite from "./pages/Favorite";
import Library from "./pages/Library";
import './styles/nav.css'
import './styles/top.css'
import './styles/poster.css'
import './styles/input.css'
import './styles/profile.css'

export default function Navigation() {
  return (
    <>
      <header className="nav">
        <div className="head-wrapper header-height">
          <div className="title-wrapper center header-height">
            <h2>Naxocist</h2>
          </div>
          <Link className="nav-link center header-height" to="/library">
            Library
          </Link>
          <Link className="nav-link center header-height" to="/favorite">
            Favorite
          </Link>
          <Link className="nav-link center header-height" to="/">
            Home
          </Link>
        </div>
      </header>
      <div className="panel">
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/favorite">
            <Favorite />
          </Route>
          <Route path="/library">
            <Library />
          </Route>
        </Switch>
      </div>
    </>
  );
}
