import React, { useState, useEffect } from "react";
import RotateLoader from "react-spinners/RotateLoader";
import FadeIn from "react-fade-in";


var url = 'https://myanimelist.net/profile/Naxocist';
var pic = '';

export default function Home() {
  const [top, setTop] = useState([]);
  const [loading, setLoading] = useState([false]);
  const [search, setSearch] = useState("");
  const [results, setResults] = useState([]);

  const onKeyDown = (event) => {
    const keyword = event.target.value;
    if (event.key === "Enter" && keyword) {
      setSearch('');
      fetch(`https://api.jikan.moe/v3/search/anime?q=${keyword}&page=1`)
        .then((res) => res.json())
        .then((data) => {
          if (data.results) {
            setResults(data.results);
          }
        });
    }
  };


  useEffect(() => {
    setLoading(true);
    fetch(`https://api.jikan.moe/v3/user/Naxocist`)
      .then(res => res.json())
      .then(data => {
        pic = data.image_url;
      })

    fetch(`https://api.jikan.moe/v3/top/anime/1/airing`)
      .then(res => res.json())
      .then(data => {
        setTop(data.top.slice(0, 5));
        setLoading(false);
      });

  }, []);

  return (
    <React.Fragment>
      <FadeIn>
        <header className="top-header">Profile</header>
      </FadeIn>

      <div className="profile">
        <a href={url} target="_blank" className="profile-url" rel="noreferrer">
          <img className="profile-pic" src={pic} alt="Naxocist's profile" />
        </a>
        <div className="info">
          <ul>
            <li>Naxocist</li>
            <li>Birthday : 9<sup>th</sup> August 2006</li>
            <li>Favorite : Little Witch Academia</li>
          </ul>
        </div>
      </div>

      <FadeIn>
        <header className="top-header">Top airing anime</header>
      </FadeIn>

      <div className="anime-wrapper">
        {loading ? (
          <div className="loader">
            <RotateLoader color={"#3895d3"} loading={loading} size={10} />
          </div>
        ) : (
          <>
            {top.map((anime, index) => (
              <div className="poster top-poster" key={index}>
                <div className="content-wrapper">
                  <a href={anime.url} target="_blank" rel="noreferrer">
                    <div className="rank-box">
                      <div className="rank">{anime.rank}</div>
                    </div>
                    <img src={anime.image_url} alt="anime-src" />
                    <div className="anime-info">
                      <h5>{anime.title}</h5>
                    </div>
                  </a>
                </div>
              </div>
            ))}

            <div className="input">
              <input
                placeholder="Search anime.."
                className="search"
                onChange={(e) => setSearch(e.target.value)}
                value={search}
                onKeyDown={onKeyDown}
              />
            </div>
          </>
        )}
      </div>

      <div className="anime-wrapper">
        {results.map((anime, index) => (
          <div className="poster top-poster" key={index}>
            <div className="content-wrapper">
              <a href={anime.url} target="_blank" rel="noreferrer">
                <div className="rank-box">
                  <div className="rank">{anime.rank}</div>
                </div>
                <img src={anime.image_url} alt="anime-src" />
                <div className="anime-info">
                  <h5>{anime.title}</h5>
                </div>
              </a>
            </div>
          </div>
        ))}
      </div>

    </React.Fragment>
  );
}
